@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
.main-nav-container .app-bar{box-shadow:0px 2px 4px -1px rgba(0,0,0,0.2),0px 4px 5px 0px rgba(0,0,0,0.14),0px 1px 10px 0px rgba(255,255,255,0.12)}.main-nav-container .nav-links{display:flex;justify-content:center;align-items:center;min-width:100%}.main-nav-container .nav-links a{text-align:center;padding:10px;font-size:0.8rem}.main-nav-container .nav-links a:hover{background:rgba(255,255,255,0.15)}@media (min-width: 600px){.main-nav-container .nav-links a{font-size:inherit;padding:20px}}

.jumbotron{display:flex;justify-content:center;flex-direction:column;background-color:#0e0e0e;background:radial-gradient(ellipse at 60% 48%, rgba(255,255,255,0.07) 0%, rgba(14,14,14,0) 60%);height:calc(100vh - 72px);overflow:hidden}@-moz-document url-prefix(){.jumbotron{background:unset}}@media (min-aspect-ratio: 3 / 2){.jumbotron{flex-direction:row}}.jumbotron .MuiTypography-root{display:flex;justify-content:center;align-items:center;flex-direction:column;color:#fff;flex-basis:100%;text-align:center}@media (min-aspect-ratio: 3 / 2){.jumbotron .MuiTypography-root{flex-grow:0;flex-shrink:0;flex-basis:40%}}@media (min-aspect-ratio: 3 / 2){.jumbotron .MuiTypography-root{align-items:flex-end}}.jumbotron .MuiTypography-root .name{font-size:4rem;font-weight:200;line-height:1;padding:0 20px}@media (min-aspect-ratio: 3 / 2){.jumbotron .MuiTypography-root .name{font-size:5rem}}@media (min-aspect-ratio: 3 / 2) and (max-width: 1280px){.jumbotron .MuiTypography-root .name{font-size:4rem}}@media (min-aspect-ratio: 3 / 2) and (max-width: 1024px){.jumbotron .MuiTypography-root .name{font-size:2.5rem}}.jumbotron .MuiTypography-root .title{font-size:1.5rem;line-height:1;font-weight:500}@media (min-aspect-ratio: 3 / 2){.jumbotron .MuiTypography-root .title{font-size:1rem;padding-right:30px}}@media (min-aspect-ratio: 3 / 2) and (min-width: 1024px){.jumbotron .MuiTypography-root .title{font-size:1.6rem;padding-right:40px}}.jumbotron .img{display:flex;justify-content:flex-end;align-items:flex-start;flex-direction:column}.jumbotron .img img{max-height:100%}@-moz-document url-prefix(){.jumbotron .img img{-webkit-filter:drop-shadow(0px 0px 40px rgba(255,255,255,0.1));filter:drop-shadow(0px 0px 40px rgba(255,255,255,0.1))}}@media (min-aspect-ratio: 9 / 10) and (max-aspect-ratio: 960 / 641){.jumbotron .img img{max-width:80%;margin:0 10%}}@media (min-aspect-ratio: 1 / 1) and (max-aspect-ratio: 960 / 641){.jumbotron .img img{max-width:50%;margin:0 25%}}

.about-cont{display:flex;justify-content:center;align-items:center;flex-direction:column;background-color:#cdcdcd;padding:20px 50px}.about-cont h2{font-size:3rem;font-weight:800;padding:20px;letter-spacing:5px}@media (min-width: 960px){.about-cont{align-items:flex-end}}@media (min-width: 960px){.about-cont .content{display:flex;justify-content:center;align-items:center}}.about-cont .content>*{padding:20px}@media (min-width: 960px){.about-cont .content>*{padding:20px 40px}}.about-cont .img{display:flex;justify-content:center;align-items:center}@media (min-width: 960px){.about-cont .img{flex:0 0 33%}}.about-cont .text>*{margin:20px 0}.about-cont .name-title{display:flex;justify-content:center;align-items:center;flex-direction:column}@media (min-width: 960px){.about-cont .name-title{align-items:flex-start;margin-top:0}}.about-cont .name-title *{display:block;font-style:italic;text-align:center}.about-cont .name{font-size:2.5rem;line-height:2.5rem}.about-cont .title{font-size:1.25rem;font-weight:200;line-height:1.5rem}.about-cont .social.linkedin:active svg,.about-cont .social.linkedin:focus svg,.about-cont .social.linkedin:hover svg{box-shadow:0 0 5px rgba(0,0,0,0.3);color:#0077b5}.about-cont .contact-loc{display:flex;justify-content:space-between;align-items:center;background-color:#ddd;margin:10px -10px;padding:10px}@media (min-width: 960px){.about-cont .contact-loc{max-width:370px;margin:10px 0}}.about-cont .contact-loc a{display:flex;justify-content:center;align-items:center}.about-cont .contact-loc span{font-size:0.8rem}.about-cont .overview p{font-size:0.8rem}

*{box-sizing:border-box;padding:0;margin:0;transition:0.25s}body{background-color:#0e0e0e;width:100vw}div,header,section,article{width:100%}img{max-width:100%}div{height:100%}p{margin:20px 0}

